import Link from "next/link";
import React from "react";

interface JogarDeNovoProps {
  onRestart: () => void;
}

const JogarDeNovo: React.FC<JogarDeNovoProps> = ({ onRestart }) => {
  return (
    <div style={{
      position: 'absolute',
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: 'rgba(0, 0, 0, 0.9)', 
      zIndex: 10,
    }}>
      <h2 style={{ color: "yellow", fontSize: "2.5em", marginBottom: "30px", textShadow: "0 0 10px yellow" }}>
        GAME OVER!
      </h2>
      
      <div style={{ display: "flex", flexDirection: "column", gap: "15px", width: "80%", maxWidth: "250px" }}>
        
        {/* Botão de JOGAR DE NOVO (Efeito Hover Amarelo) */}
        <button
          onClick={onRestart}
          style={{
            background: "white", // Cor normal: Branco
            color: "black",
            padding: "12px 20px",
            border: "none",
            borderRadius: "5px",
            cursor: "pointer",
            fontSize: "1.2em",
            fontWeight: "bold",
            transition: "background 0.2s, transform 0.1s",
            boxShadow: "0 4px 0 #333"
          }}
          // --- LÓGICA DO HOVER AMARELO ---
          onMouseEnter={(e) => {
            e.currentTarget.style.background = 'yellow';
            e.currentTarget.style.boxShadow = '0 4px 0 #ccaa00';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.background = 'white'; // Volta para branco
            e.currentTarget.style.boxShadow = '0 4px 0 #333';
          }}
          title="Inicia uma nova partida"
        >
          JOGAR DE NOVO
        </button>

        {/* Link para VOLTAR AO MENU (Efeito Hover Amarelo) */}
        <Link 
          href="/"
          style={{
            color: "white",
            textDecoration: "none",
            padding: "12px 20px",
            border: "2px solid white",
            borderRadius: "5px",
            fontSize: "1.1em",
            fontWeight: "bold",
            transition: "background 0.2s, color 0.2s, border-color 0.2s"
          }}
          // --- LÓGICA DO HOVER AMARELO ---
          onMouseEnter={(e) => {
            e.currentTarget.style.backgroundColor = 'yellow';
            e.currentTarget.style.color = 'black';
            e.currentTarget.style.borderColor = 'yellow';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.backgroundColor = 'transparent'; // Volta para transparente
            e.currentTarget.style.color = 'white';
            e.currentTarget.style.borderColor = 'white';
          }}
        >
          VOLTAR AO MENU
        </Link>
      </div>
    </div>
  );
}

export default JogarDeNovo;